.listing{
    text-align: center;
}
.listing h2{
    margin-bottom: 40px;
    margin-top: 30px;
}
.list_col {
    position: relative;
    margin-bottom: 40px;
}
.listPrice {
    position: absolute;
    top: 0;
    left: 12px;
    min-width: 50px;
    height: 30px;
    background: #00aff0;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0;
    color: #fff;
    font-weight: bold;
    font-size: 14px;
    padding: 5px;
}
.listName{
    font-weight: bold;
}
figure {
    overflow: hidden;
}
figure img{
    transition: all 0.3s ease;
    transform: scale(1);
}
figure:hover img{
    transform: scale(1.1);
}

